import {
  Component, OnChanges, Input, OnInit,
  Output, EventEmitter, ViewChild, ElementRef
} from '@angular/core';

import { StorageAndUtilsService } from "../shared/storageAndUtils.service";
import { NgbModal, NgbDropdownConfig, ModalDismissReasons } from '@ng-bootstrap/ng-bootstrap';


@Component({
  selector: 'app-star',
  templateUrl: './star.component.html',
  styleUrls: ['./star.component.css'],
  providers: [NgbDropdownConfig]
})

export class StarComponent implements OnInit, OnChanges {

  @Input() rating: number;
  @Output() ratingClick: EventEmitter<any> = new EventEmitter<any>();
  @Input() selectedData: any;

  inputName: string;
  ratingContext: any;
  ratingModel: any = { message: "" };

  constructor(private storageAndUtilsService: StorageAndUtilsService,
              private modalService: NgbModal) {
    storageAndUtilsService.getContext().subscribe((d) => {
      // if (d.type == "sendRating") {
      //   console.log(d);
      // }
    });
  }

  closeResult: string;

  @ViewChild('content') content:ElementRef;

  private getDismissReason(reason: any): string {
    if (reason === ModalDismissReasons.ESC) {
      return 'by pressing ESC';
    } else if (reason === ModalDismissReasons.BACKDROP_CLICK) {
      return 'by clicking on a backdrop';
    } else {
      return `with: ${reason}`;
    }
  }

  ngOnInit() {
    this.inputName = '_rating';
    this.rating = 0;
  }

  onClick(rating: number,content:any): void {
    this.rating = rating;

    if (rating < 3) {
      this.modalService.open(content, { ariaLabelledBy: 'modal-basic-title' }).result.then((mailModel) => {
        this.closeResult = mailModel.message;// this.ratingModel.message ;
        console.log( this.closeResult );
        this.ratingClick.emit({
          rating: rating
        });
        
        this.storageAndUtilsService.sendContext({
          type: "sendRating", rating: rating, data: this.ratingContext, reason:this.closeResult
        });
      }, (reason) => {
        console.log(reason);
      });
    }



  }

  ngOnChanges(change: any) {
    console.log(change);
    this.ratingContext = change.selectedData.currentValue;
  }

}
