import { Component, OnInit, ViewChild, ElementRef, Input } from '@angular/core';
import { ActivatedRoute } from "@angular/router";
import { Location } from '@angular/common';
import { NgbModal, NgbDropdownConfig, ModalDismissReasons } from '@ng-bootstrap/ng-bootstrap';
import { HttpService, WebsocketService, StorageAndUtilsService, LoggerService, GlobalConstants } from '../shared';


@Component({
  selector: 'app-home',
  templateUrl: './home.component.html',
  styleUrls: ['./home.component.css'],
  providers: [NgbDropdownConfig]

})
export class HomeComponent implements OnInit {

  showChatBotWrapper: boolean = false;

  @ViewChild('scrollMe') private myScrollContainer: ElementRef;
  @ViewChild('chatWindow') private chatWindow: ElementRef;

  closeResult: string;
  expandFlag: boolean = false;
  toggleVolumeFlag: boolean = true;
  toggleMenu: boolean = false;
  sideMenu: any = GlobalConstants.arrSideMenu;

  contact: any = {
    "email": GlobalConstants.dictAppSettingsMaster.getItem("CONST_CONTACT_EMAIL")
  };

  constructor(private _location: Location,
    private actRoute: ActivatedRoute,
    private storageAndUtilsService: StorageAndUtilsService,
    private modalService: NgbModal,
    private config: NgbDropdownConfig,
    private httpService: HttpService,
    private wsService: WebsocketService,
    private loggerService: LoggerService) {
    try {

      console.log("contact", this.contact);

      config.placement = 'bottom-left';
      config.autoClose = false;
      this.sideMenu = GlobalConstants.arrSideMenu;
      console.log("Inside CTOR this.sideMenu", this.sideMenu);
      this.actRoute.params.subscribe((params: any) => {
        this.showChatBotWrapper = params["openChat"];
      })
    } catch (error) {
      this.loggerService.log('error', "[Component] --> [] --> " + JSON.stringify(error));
    }
  }

  open(content, sideMenuKey) {

    if (sideMenuKey == "A") {

      this.modalService.open(content, { ariaLabelledBy: 'modal-basic-title' }).result.then((mailModel) => {
        this.closeResult = `Closed with: ${mailModel}`;
      }, (reason) => {
        this.closeResult = `Dismissed ${this.getDismissReason(reason)}`;
      });

    } else if (sideMenuKey == "MS") {
      
      this.contact.email = GlobalConstants.dictAppSettingsMaster.getItem("CONST_CONTACT_EMAIL");
      this.modalService.open(content, { ariaLabelledBy: 'modal-basic-title' }).result.then((mailModel) => {

        this.httpService.post("/sendmail", mailModel).subscribe((resMailSent) => {
          console.log("resMailSent", resMailSent);
        });

        this.closeResult = `Closed with: ${mailModel}`;
      }, (reason) => {
        this.closeResult = `Dismissed ${this.getDismissReason(reason)}`;
      });

    } else if (sideMenuKey == "N") {

      this.httpService.post("/commentsOperations",{opType:0}).subscribe((d:any) => {
          
        console.log("DAtaaaaaaa:- ", d);
        this.comments = d;
      });

      this.modalService.open(content, { ariaLabelledBy: 'modal-basic-title' }).result.then((notificationsModel) => {

        this.closeResult = `Closed with: ${notificationsModel}`;
      }, (reason) => {
        this.closeResult = `Dismissed ${this.getDismissReason(reason)}`;
      });
    }


  }

  comments :any;

  private getDismissReason(reason: any): string {
    if (reason === ModalDismissReasons.ESC) {
      return 'by pressing ESC';
    } else if (reason === ModalDismissReasons.BACKDROP_CLICK) {
      return 'by clicking on a backdrop';
    } else {
      return `with: ${reason}`;
    }
  }

  toggleHamMenu() {
    this.toggleMenu = !this.toggleMenu;
    console.log("Hello toggleMenu:- ", this.toggleMenu);

  }

  ngOnInit() {
    // this.scrollToBottom();
  }

  CloseChatBotWrapper() {
    this.wsService.closeWebSocket();
    this.showChatBotWrapper = false;
    this._location.back();
  }

  ngAfterViewChecked() {
    this.scrollToBottom();
  }

  scrollToBottom(): void {
    try {
      this.myScrollContainer.nativeElement.scrollTop = this.myScrollContainer.nativeElement.scrollHeight;
    } catch (err) {
      this.loggerService.log('error', "[HomeComponent] --> [scrollToBottom()] --> " + JSON.stringify(err));
    }
  }

  sendContext(type) {
    try {
      this.storageAndUtilsService.sendClearChat({ type: type });
    } catch (error) {
      this.loggerService.log('error', "[HomeComponent] --> [clearChat()] --> " + JSON.stringify(error));
    }
  }

  openDialog() {
    console.log("Email Dialog Opened");
  }

  toggleExpand() {
    this.expandFlag = !this.expandFlag;

    // console.log(this.expandFlag);
    if (this.expandFlag) {
      this.chatWindow.nativeElement.classList.remove("collapseChatWindow");
      this.chatWindow.nativeElement.classList.add("expandChatWindow");
    } else {
      this.chatWindow.nativeElement.classList.add("collapseChatWindow");
      this.chatWindow.nativeElement.classList.remove("expandChatWindow");
    }
  }

  toggleVolume() {
    this.toggleVolumeFlag = !this.toggleVolumeFlag;
    GlobalConstants.volumeFlag = this.toggleVolumeFlag;
  }

  dropDownOpen(e) {
    this.sideMenu = GlobalConstants.arrSideMenu;
  }

  sideMenuClicked(sm: any, content: any) {

    var menuKey = sm.menuKey
    console.log(menuKey);
    switch (menuKey) {
      case ("KPI"):
        this.sendContext("KPI");
        break;
      case ("TQ"):
        break;
      case ("MA"):
        this.toggleVolume();
        break;
      case ("DG"):
        break;
      case ("SB"):
        break;
      case ("LB"):
        break;
      case ("DB"):
        break;
      case ("CC"):
        this.sendContext("CC");
        break;
      case ("MS"):
        this.open(content, "MS");
        break;
      case ("FS"):
        this.toggleExpand();
        break;
      case ("H"):
        break;
      case ("LO"):
        this.CloseChatBotWrapper();
        break;
      case ("N"):
        this.sendContext("N");
        this.open(content, "N");
        break;
      case ("A"):
        this.sendContext("A");
        this.open(content, "A");
        break;

    }
  }
}
